import React from 'react'

import "./replyDialog.css"

function replyDialog(props){
    return(
        <div className="advice-reply-dialog">
            <div>
                <span>栎工坊</span>
                <a onClick={props.onReplyDialog.bind(null,false)}>
                    <svg className="icon" width="18px" height="18.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                        <path fill="#2c2c2c" d="M547.2 512l416-416c9.6-9.6 9.6-25.6 0-35.2s-25.6-9.6-35.2 0l-416 416-416-416c-9.6-9.6-25.6-9.6-35.2 0s-9.6 25.6 0 35.2l416 416-416 416c-9.6 9.6-9.6 25.6 0 35.2s25.6 9.6 35.2 0l416-416 416 416c9.6 9.6 25.6 9.6 35.2 0s9.6-25.6 0-35.2L547.2 512z" />
                    </svg>
                </a>
            </div>
            <div>
                <p>感谢您的反馈</p>
                <a href="javascript:void(0)" onClick={props.onReplyDialog.bind(null,false)}>确定</a>
            </div>
        </div>
    )
}
export default replyDialog


