import React,{Component} from 'react'

import {stuffInfo} from './infoStorege'
import './stuff.css'
import './title_public.css'
import copyState from './copyState'

const {imgLink} = stuffInfo()
const {text}=stuffInfo()

class stuff extends Component{
    constructor(props){
        super()
        this.state={
            column:1
        }
    }
    changeColumn(val){
        console.log(typeof val)
        let copystate=copyState(this.state)
        if(typeof val ==="number"){
            copystate.column=val
        }else if(typeof val==="boolean"){
            val?copystate.column+=1:copyState.column-=1            
        }
        this.setState(copystate)
    }
    render(){
        let content=this.props.currentpage===1?text.page1.content:text.page2.content
        let splitword=content.split('，')
        splitword.forEach((curVal,index,arr)=>{
            curVal+='，'
        })
        let wordInsert=<p>{splitword[0]?splitword[0]:null}<br/>
        {splitword[1]?splitword[1]:null}<br/>
        {splitword[2]?splitword[2]:null}<br/>
        {splitword[3]?splitword[3]:null}<br/>
        {splitword[4]?splitword[4]:null}</p>

        let page=this.props.currentpage===1?imgLink.page1:imgLink.page2
        let imgInsert=[]
            switch (this.state.column%4) {
                case 0:
                    imgInsert=[
                        page.slideshow04,
                        page.slideshow01,
                        page.slideshow02,
                        page.slideshow03
                    ]
                    break;
                case 1:
                    imgInsert=[
                        page.slideshow01,
                        page.slideshow02,
                        page.slideshow03,
                        page.slideshow04
                ]
                break;
                case 2:
                    imgInsert=[
                        page.slideshow02,
                        page.slideshow03,
                        page.slideshow04,
                        page.slideshow01
                    ]
                    break;
                case 3:
                    imgInsert=[
                        page.slideshow03,
                        page.slideshow04,
                        page.slideshow01,
                        page.slideshow02
                ]
                break
                default:
                    break;
            }
    return(
        <div className="stuff">
            <div className="container">
                <img src={require(`${this.props.currentpage===1?imgLink.page1.title:imgLink.page2.title}`)} alt=""/>
                <div className={this.props.currentpage===1?"title":"title four-letter-title"}>
                    <span className="en-title">{this.props.currentpage===1?text.page1.EN_Maintitle:text.page2.EN_Maintitle}</span>
                    <div className={this.props.currentpage===1?"cn-title cn-title-stuff-page1":"cn-title cn-title-stuff-page2"}>
                        <hr/>
                        <div></div>
                        <h1>{this.props.currentpage===1?text.page1.CN_Maintitle:text.page2.CN_Maintitle}</h1>
                        <div></div>
                        <hr/>
                    </div>
                </div>
            </div>
            <div className="stuff-describe">
                <h2>{this.props.currentpage===1?text.page1.title:text.page2.title}</h2>
                <hr/>
                {wordInsert}
            </div>
            <div>
                <ul className="stuff-slideshow">
                    <li>
                        <img src={require(`${imgInsert[0]}`)}  alt=""/>
                        <div onClick={this.changeColumn.bind(this,false)}></div>
                    </li>
                    <li>
                        <img src={require(`${imgInsert[1]}`)} alt=""/>
                        {this.props.currentpage===1?
                            <div className="stuff-describe-text">
                                <span>{text.page1.columnTitle}</span>
                                <p>{text.page1.columnContent}</p>
                            </div>
                            :null}
                        </li>
                    <li>
                        <img src={require(`${imgInsert[2]}`)} alt=""/>
                        <div onClick={this.changeColumn.bind(this,true)}></div>
                    </li>
                </ul>
                <ul className="stuff-slideshow-bullet">
                    <li className={this.state.column%4===0?"current":null} onMouseEnter={this.changeColumn.bind(this,0)}></li>
                    <li className={this.state.column%4===1?"current":null} onMouseEnter={this.changeColumn.bind(this,1)}></li>
                    <li className={this.state.column%4===2?"current":null} onMouseEnter={this.changeColumn.bind(this,2)}></li>
                    <li className={this.state.column%4===3?"current":null} onMouseEnter={this.changeColumn.bind(this,3)}></li>
                </ul>
            </div>
        </div>
        )
    }
}
export default stuff


