import React from 'react'

import Contact from './contact'
import Shop from './shop'
import PurchaseDetail from './purchaseDetail'
import GoodsCategory from './goodsCategory'
import ShoppingList from './shoppingList'

function otherPage(props){
    let mainState=props.rootState
    return(
        <div>
            {mainState.mainpage===3&&mainState.currentPageLevel===0?<Contact/>:null}
            {mainState.mainpage===5&&mainState.currentPageLevel===0?<Shop onToTheSubpage={props.onToTheSubpage.bind(null)}/>:null}
            {mainState.purchaseDetail===true&&mainState.currentPageLevel===1?<PurchaseDetail/>:null}
            {mainState.goodsCategory===true&&mainState.currentPageLevel===1?<GoodsCategory onAddShopList={props.onAddShopList.bind(null)}/>:null}
            {mainState.shoppingList===true&&mainState.currentPageLevel===1?<ShoppingList/>:null}
        </div>
    )
}
export default otherPage


