import React, { component } from 'react'

import {cultureInfo} from './infoStorege'
import './culture.css'
import './title_public.css'

const {text}=cultureInfo()
const {imgLink}=cultureInfo()

function culture(props) {
    return (
        <div className="culture container">
            <div className="title">
                <span className="en-title">{props.currentpage===2?text.page2.subTitle:text.page4.subTitle}</span>
                <div className="cn-title cn-title-page2-culture">
                    <hr/>
                    <div></div>
                    <h1>{props.currentpage===2?text.page2.title:text.page4.title}</h1>
                    <div></div>
                    <hr/>
                </div>
            </div>
            <ul className="culture-column">
                <li>
                    <h1>{props.currentpage===2?text.page2.sectionTitle:text.page4.sectionTitle}</h1>
                    <hr/>
                    <p>{props.currentpage===2?text.page2.sectionContent:text.page4.sectionContent}</p>
                    <a href="javscript:void(0)">MORE</a>
                </li>
                <li>
                    <img src={require(`${props.currentpage===2?imgLink.page2.firstPage:imgLink.page4.firstPage}`)} alt="" height={400} width={600}/>
                </li>
                <li>
                    <img src={require(`${props.currentpage===2?imgLink.page4.secPage:imgLink.page4.secPage}`)} alt=""/>
                </li>
                <li>
                    <p>{props.currentpage===2?text.page2.sectionPassage1:text.page4.sectionPassage1}</p>
                    <p>{props.currentpage===2?text.page2.sectionPassage2:text.page4.sectionPassage2}</p>
                </li>
            </ul>
        </div>
    )
}
export default culture


