import React, { Component } from 'react';

import './App.css';
import HeaderBack from './headerBackground'
import TopNav from './topNav'
import Footer from './footer'
import OtherPage from './otherPage'
import RugularPage from './regularPage'
import copyState from './copyState'


class App extends Component {
  constructor(props){
    super()
    this.state={
      mainpage:0,
      currentPageLevel:0,
      purchaseDetail:false,
      goodsCategory:false,
      shoppingList:false
    }
  }
  render() {
    let secPageImage=this.state.currentPageLevel===0?
    <img src={require("./images/header-page/mainpage3.jpg")} alt=""/>:
    <img src={require("./images/header-page/secPage.jpg")} alt=""/>
    return (
      <div className="root">
        <header>  
            <TopNav shopList={this.state.shopList}
            onChangePage={this.toOtherPage.bind(this)}
            currentpage={this.state.mainpage}
            onToTheSubpage={this.toTheSubpage.bind(this)}
            />
          {this.state.purchaseDetail===true||
          this.state.mainpage===3?
          <div className="second-page">
           {secPageImage}
          </div>:(this.state.currentPageLevel===0?
          <HeaderBack/>:null)}
        </header>
        <article>
          {(this.state.currentPageLevel!==0||
          this.state.mainpage===3||this.state.mainpage===5)?
          <OtherPage rootState={this.state} 
          onAddShopList={this.addShopList.bind(this)}
          onToTheSubpage={this.toTheSubpage.bind(this)}/>
          :<RugularPage currentpage={this.state.mainpage}/>}
        </article>
        <footer>
          <Footer onToMarket={this.toOtherPage.bind(this)} 
          onToContact={this.toOtherPage.bind(this)}/>
        </footer>
      </div>
    );
  }
  toOtherPage(val){
    let copystate=copyState(this.state)
    copystate.mainpage=val
    copystate.purchaseDetail=false
    copystate.goodsCategory=false
    copystate.shoppingList=false
    copystate.currentPageLevel=0
    this.setState(copystate)
  }
  addShopList(goods){
    let copystate=copyState(this.state)
    copystate.shopList.push(goods)
    this.setState(copystate)
  }
  toTheSubpage(page){
    let copystate=copyState(this.state)
    copystate.currentPageLevel=1
    copystate[page]=true
    this.setState(copystate)
  }
}

export default App;



