import React from 'react'

import {goodsInfo} from './infoStorege'
import './goods.css'

const {imgLink}=goodsInfo() 
const {text}=goodsInfo()

function goods(props){
    return (
        <div className="goods container">
            <div className="title">
                <span className="en-title">{props.currentpage===0?text.page0.EN_Maintitle:text.page4.EN_Maintitle}</span>
                <div className={props.currentpage===0?"cn-title":"cn-title cn-title-page4-goods"}>
                    <hr/>
                    <div></div>
                    <h1>{props.currentpage===0?text.page0.CN_Maintitle:text.page4.CN_Maintitle}</h1>
                    <div></div>
                    <hr/>
                </div>
            </div>
            <div>
                <img src={require(`${props.currentpage===0?imgLink.page0:imgLink.page5}`)} alt=""/>
            </div>
            <div className="goods-column">
                <div>
                    <h2>{props.currentpage===0?text.page0.title01:text.page4.title01}</h2>
                    <span>{props.currentpage===0?text.page0.en_title01:text.page4.en_title01}</span>
                    <hr/>
                    <p>{props.currentpage===0?text.page0.content01:text.page4.content01}</p>
                    <a href="javascript:void(0)">MORE</a>
                </div>
                <div>
                    <h2>{props.currentpage===0?text.page0.title02:text.page4.title02}</h2>
                    <span>{props.currentpage===0?text.page0.en_title02:text.page4.en_title02}</span>
                    <hr/>
                    <p>{props.currentpage===0?text.page0.content02:text.page4.content02}</p>
                    <a href="javascript:void(0)">MORE</a>
                </div>
            </div>
        </div>
    )
}
export default goods


