import React from 'react'

import Culture from './culture'
import AboutUs from './aboutUs'
import Brand from './brand'
import Story from './story'
import Goods from './goods'
import Stuff from './stuff'

function main(props) {
    return(
        <div>
            {props.currentpage<2?<AboutUs/>:<Culture currentpage={props.currentpage}/>}
            {props.currentpage!==2?<Brand currentpage={props.currentpage}/>:<Story currentpage={props.currentpage}/>}
            {props.currentpage===0||props.currentpage===4?<Goods currentpage={props.currentpage}/>:<Stuff currentpage={props.currentpage}/>}
        </div>
    )
}
export default main


