import React,{Component} from 'react'
import copyState from './copyState'

import './headerbackground.css'

class headerBackground extends Component{
    constructor(props){
        super()
        this.state={
            page:0,
            imageLock:false
        }
    }
    componentWillMount(){
        let state=copyState(this.state),
            that=this
        this.timer=setInterval(function() {
            state.page+=1            
            that.setState(state)
        }, 5000);
    }
    bulletHandle(val){
        let state=copyState(this.state)
        state.page=val
        this.setState(state)
    }
    render(){
        let infoInsert={
            bgImage:`./images/header-page/headerSlide0${this.state.page%5+1}.jpg`,
            tag:`./images/header-page/background_tag0${this.state.page%5+1}.png`
        }
        return(
            <div className="background">
                <img src={require(`${infoInsert.bgImage}`)} alt=""/>
                <div className="container">
                    <img className="tag-img" src={require(`${infoInsert.tag}`)} alt=""/>
                    <ul className="bullet">
                        <li className={this.state.page%5===0?"current":null} onMouseEnter={this.bulletHandle.bind(this,0)}></li>
                        <li className={this.state.page%5===1?"current":null} onMouseEnter={this.bulletHandle.bind(this,1)}></li>
                        <li className={this.state.page%5===2?"current":null} onMouseEnter={this.bulletHandle.bind(this,2)}></li>
                        <li className={this.state.page%5===3?"current":null} onMouseEnter={this.bulletHandle.bind(this,3)}></li>
                        <li className={this.state.page%5===4?"current":null} onMouseEnter={this.bulletHandle.bind(this,4)}></li>
                    </ul>
                </div>
            </div>
        )
    }
    componentWillUnmount(){
        clearInterval(this.timer)
    }
}
export default headerBackground


