import React from 'react'

import InfoVideo from './infoVideo'
import './aboutUs.css'

function aboutUs(props){
    return(
        <div className="aboutus-container aboutus">
            <hr/>
            <div className="information-aboutus">
                <InfoVideo/>
                <div className="self-introduce">
                    <span>ABOUT US</span>
                    <div className="aboutus-title">
                        <h1>关于我们</h1>
                        <hr/>
                        <div></div>
                    </div>
                    <div>
                        <p>
                            “栎工坊”文创品牌成立于2017年，由吉林动画学院创立。
                            凭借动画学院多年以来在数位内容创新的强大资源，以
                            丰富多元的设计风格为本，强调人文设计以及环境友善
                            的天然材料选择，以平凡的日常生活为出发点融合木材
                            的温暖触感，希望带给消费者前所未有的品牌体验<br/>
                            ......
                        </p>
                        <a href="javascript:void(0)">MORE</a>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default aboutUs


