import React,{Component} from 'react'

import {brandInfo} from './infoStorege'
import copyState from './copyState'
import './brand.css'
import './title_public.css'

const {text}=brandInfo()
const {imgLink}=brandInfo()

class brand extends Component{
    constructor(props){
        super()
        this.state={
            positionColumnSwitch:false
        }
    }
    switchColumn(){
        let copystate=copyState(this.state)
        copystate.positionColumnSwitch=this.state.positionColumnSwitch?false:true
        this.setState(copystate)
    }
    render(){
        return (
            <div className="container">
                <section className="position back-color" id="section1">
                    <div className="title">
                        <span className="en-title">{(this.props.currentpage===0?text.page0.brandposition.EN_Maintitle:this.props.currentpage===1?text.page1.handwork.EN_Maintitle:text.page4.couple.EN_Maintitle)}</span>
                        <div className={this.props.currentpage===4?"cn-title cn-title-page4":"cn-title"}>
                            <hr/>
                            <div className="circle-decorate"></div>
                            <h1>{this.props.currentpage===0?text.page0.brandposition.CN_Maintitle:this.props.currentpage===1?text.page1.handwork.CN_Maintitle:text.page4.couple.CN_Maintitle}</h1>
                            <div className="circle-decorate"></div>
                            <hr/>
                        </div>
                    </div>
                    <div className="position-slideshow">
                        <div className="position-column-list-warp">
                            <ul className="position-column-list">
                                <li>
                                    <img src={require(`${this.state.positionColumnSwitch?
                                    (this.props.currentpage===0?imgLink.page0.brandposition.slideshow01:
                                        this.props.currentpage===1?imgLink.page1.handwork.slideshow01:
                                        imgLink.page4.couple.slideshow01):
                                        (this.props.currentpage===0?imgLink.page0.brandposition.slideshow03:
                                        this.props.currentpage===1?imgLink.page1.handwork.slideshow03:
                                        imgLink.page4.couple.slideshow03)}`)} alt=""/>
                                    <h2>{this.state.positionColumnSwitch?
                                    (this.props.currentpage===0?text.page0.brandposition.title01:
                                        this.props.currentpage===1?text.page1.handwork.title01:
                                        text.page4.couple.title01):
                                    (this.props.currentpage===0?text.page0.brandposition.title03:
                                    this.props.currentpage===1?text.page1.handwork.title03:
                                    text.page4.couple.title03)}</h2>
                                    <hr/>
                                    <p>{this.state.positionColumnSwitch?
                                    (this.props.currentpage===0?text.page0.brandposition.content01:
                                        this.props.currentpage===1?text.page1.handwork.content01:
                                        text.page4.couple.content01):
                                    (this.props.currentpage===0?
                                    text.page0.brandposition.content03:this.props.currentpage===1?
                                    text.page1.handwork.content03:text.page4.couple.content03)}</p>
                                </li>
                                <li>
                                    <img src={require(`${this.state.positionColumnSwitch?
                                    (this.props.currentpage===0?imgLink.page0.brandposition.slideshow02:
                                        this.props.currentpage===1?imgLink.page1.handwork.slideshow02:
                                        imgLink.page4.couple.slideshow02):
                                    this.props.currentpage===0?imgLink.page0.brandposition.slideshow04:
                                    this.props.currentpage===1?imgLink.page1.handwork.slideshow04:
                                    imgLink.page4.couple.slideshow04}`)} alt=""/>
                                    <h2>{this.state.positionColumnSwitch?
                                    (this.props.currentpage===0?text.page0.brandposition.title02:
                                        this.props.currentpage===1?text.page1.handwork.title02:
                                        text.page4.couple.title02):
                                    (this.props.currentpage===0?text.page0.brandposition.title04:
                                    this.props.currentpage===1?text.page1.handwork.title04:
                                    text.page4.couple.title04)}</h2>
                                    <hr/>
                                    <p>{this.state.positionColumnSwitch?
                                    (this.props.currentpage===0?text.page0.brandposition.content02:
                                        this.props.currentpage===1?text.page1.handwork.content02:
                                        text.page4.couple.content02):
                                    (this.props.currentpage===0?text.page0.brandposition.content04:
                                    this.props.currentpage===1?text.page1.handwork.content04:
                                    text.page4.couple.content04)}</p>
                                </li>
                            </ul>
                            <div className="position-switch-left">
                                <svg className="position-icon" onClick={this.switchColumn.bind(this)} width="60px" height="200.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                    <path fill="#b0b0b0" d="M532.526499 904.817574L139.506311 511.797385 532.526499 118.777197c12.258185-12.258185 12.432147-32.892131-0.187265-45.51052-12.707416-12.707416-32.995485-12.703323-45.511543-0.187265L75.166957 484.739123c-7.120165 7.120165-10.163477 17.065677-8.990768 26.624381-1.500167 9.755178 1.5104 20.010753 8.990768 27.491121l411.660734 411.660734c12.258185 12.258185 32.892131 12.432147 45.511543-0.187265 12.707416-12.707416 12.7023-32.995485 0.187265-45.51052z" />
                                </svg>
                            </div>
                            <div className="position-switch-right">
                                <svg className="position-icon" onClick={this.switchColumn.bind(this)} width="60px" height="200.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                    <path fill="#b0b0b0" d="M492.675886 904.817574L885.696074 511.797385 492.675886 118.777197c-12.258185-12.258185-12.432147-32.892131 0.187265-45.51052 12.707416-12.707416 32.995485-12.703323 45.511543-0.187265l411.660734 411.660734c7.120165 7.120165 10.163477 17.065677 8.990768 26.624381 1.500167 9.755178-1.5104 20.010753-8.990768 27.491121L538.374694 950.515359c-12.258185 12.258185-32.892131 12.432147-45.511543-0.187265-12.707416-12.707416-12.703323-32.995485-0.187265-45.51052z" />
                                </svg>
                            </div>
                        </div>
                    </div>
                </section>
                <section className="product">
                    <div className="series-title">
                        <span className="en-title">{this.props.currentpage===0?text.page0.product.EN_Maintitle:this.props.currentpage===1?text.page1.food.EN_Maintitle:text.page4.chopstick.EN_Maintitle}</span>
                        <div className={this.props.currentpage===0?"cn-title":this.props.currentpage===1?"cn-title cn-title-page1":"cn-title cn-title-page4"}>
                            <hr/>
                            <div></div>
                                <h1>{this.props.currentpage===0?text.page0.product.CN_Maintitle:this.props.currentpage===1?text.page1.food.CN_Maintitle:text.page4.chopstick.CN_Maintitle}</h1>
                            <div></div>
                            <hr/>
                        </div>
                    </div>
                    <ul className="product-series">
                        <li>
                            <img src={require(`${this.props.currentpage===0?imgLink.page0.product.left:this.props.currentpage===1?imgLink.page1.food.left:imgLink.page4.chopstick.left}`)} alt=""/>
                            <h2>{this.props.currentpage===0?text.page0.product.title01:this.props.currentpage===1?text.page1.food.title01:text.page4.chopstick.title01}</h2>
                            <span>{this.props.currentpage===0?text.page0.product.engTitle01:this.props.currentpage===1?text.page1.food.engTitle01:text.page4.chopstick.engTitle01}</span>
                            <hr/>
                            <p>{this.props.currentpage===0?text.page0.product.content01:this.props.currentpage===1?text.page1.food.content01:text.page4.chopstick.content01}</p>
                            <a href="javascript:void(0)">MORE</a>
                        </li>
                        <li>
                            <img src={require(`${this.props.currentpage===0?imgLink.page0.product.middle:this.props.currentpage===1?imgLink.page1.food.middle:imgLink.page4.chopstick.middle}`)} alt=""/>
                            <h2>{this.props.currentpage===0?text.page0.product.title02:this.props.currentpage===1?text.page1.food.title02:text.page4.chopstick.title02}</h2>
                            <span>{this.props.currentpage===0?text.page0.product.engTitle02:this.props.currentpage===1?text.page1.food.engTitle02:text.page4.chopstick.engTitle02}</span>
                            <hr/>
                            <p>{this.props.currentpage===0?text.page0.product.content02:this.props.currentpage===1?text.page1.food.content02:text.page4.chopstick.content02}</p>
                            <a href="javascript:void(0)">MORE</a>
                        </li>
                        <li>
                            <img src={require(`${this.props.currentpage===0?imgLink.page0.product.right:this.props.currentpage===1?imgLink.page1.food.right:imgLink.page4.chopstick.right}`)} alt=""/>
                            <h2>{this.props.currentpage===0?text.page0.product.title03:this.props.currentpage===1?text.page1.food.title03:text.page4.chopstick.title03}</h2>
                            <span>{this.props.currentpage===0?text.page0.product.engTitle03:this.props.currentpage===1?text.page1.food.engTitle03:text.page4.chopstick.engTitle03}</span>
                            <hr/>
                            <p>{this.props.currentpage===0?text.page0.product.content03:this.props.currentpage===1?text.page1.food.content03:text.page4.chopstick.content03}</p>
                            <a href="javascript:void(0)">MORE</a>
                        </li>
                    </ul>
                </section>
            </div>
        )
    }
}
export default brand


