import React,{Component} from 'react'

import ReplyDialog from './replyDialog'
import './title_public.css'
import './contact.css'

class contact extends Component{
    constructor(props){
        super()
        this.state={
            replyDialog:false
        }
    }
    toggleDialog(bol){
        this.setState({replyDialog:bol})
    }
    render(){
        return (
            <section className="contactus">
                <div className="title">
                    <span className="en-title">CONTACT US</span>
                    <div className="cn-title">
                        <hr/>
                        <div></div>
                        <h1>联系我们</h1>
                        <div></div>
                        <hr/>
                    </div>
                    <p>
                        来到“栎工坊”的空间，让人感受到身心的舒适感，消除压力，<br/>
                        我们秉持人与自然的互动，减少使用无法回收再利用的材质，<br/>
                        以木质，纸张和朴实不张扬的金属共同构建出明快流畅，<br/>
                        温暖而容易亲近的环境
                    </p>
                </div>
                <div className="map">
                    <img src={require("./images/contactLocation.jpg")} alt=""/>
                </div>
                <div className="feedback">
                    <div className="title">
                        <span className="en-title">USER FEEDBACK</span>
                        <div className="cn-title">
                            <hr/>
                            <div></div>
                            <h1>用户反馈</h1>
                            <div></div>
                            <hr/>
                        </div>
                    </div>
                    <form className="advice-form">
                        <textarea name="" id="" cols="30" rows="10" placeholder="留言栏："></textarea>
                        <input type="text"placeholder="QQ："/>
                        <input type="text" placeholder="微信："/>
                        <input type="email" placeholder="邮箱："/>
                        <input type="text" placeholder="联系电话："/>
                        <button onClick={this.toggleDialog.bind(this,true)}>立即反馈</button>
                        {this.state.replyDialog?<ReplyDialog dialog={this.state.replyDialog} onReplyDialog={this.toggleDialog.bind(this)}/>:null}
                    </form>
                </div>
            </section>
        )
    }
}

export default contact


