import React,{Component} from 'react'

import {Player} from 'video-react'
import "../node_modules/video-react/dist/video-react.css"
import "./infoVideo.css"
import playerCover from './images/playerCover.jpg'
import video from './video/preview.mp4'

class infoVideo extends Component{
    constructor(props){
        super()
        this.state={
            stopOrPause:true
        }
    }
    render(){
        return (
            <div className="player-warp">
                <Player
                    src={video}
                    poster={playerCover}
                    />
            </div>
        )
    }
}
export default infoVideo


