import React from'react'

import './searchBar.css'

function searchBar(props){
    return(
        <div className="search-bar">
            <div>
                <svg class="icon" width="20px" height="20.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                    <path fill="#ffffff" d="M547.2 512l416-416c9.6-9.6 9.6-25.6 0-35.2s-25.6-9.6-35.2 0l-416 416-416-416c-9.6-9.6-25.6-9.6-35.2 0s-9.6 25.6 0 35.2l416 416-416 416c-9.6 9.6-9.6 25.6 0 35.2s25.6 9.6 35.2 0l416-416 416 416c9.6 9.6 25.6 9.6 35.2 0s9.6-25.6 0-35.2L547.2 512z" />
                </svg>
            </div>
            <input onKeyDown={props.onKeyBoardSearch.bind(null,props)} type="text"/>
        </div>
    )
}
export default searchBar


