import React,{Component} from 'react'

import SearchBar from './searchBar'
import './topNav.css'

class topNav extends Component{
    constructor(prop){
        super()
        this.state={
            searchcolumn:false
        }
    }
    keyboardSearch(e){
        console.log(e.target)
        // if(!e.target.value) return
    }

    openSearchBar(){
        let state=this.state.searchcolumn?{searchcolumn:false}:{searchcolumn:true}
        this.setState(state)
    }

    render(){
        return(
            <nav className="banner">
                <div className="container">
                    <img src={require("./images/logo.png")} alt=""/>
                    {this.state.searchcolumn?<SearchBar onKeyBoardSearch={this.keyboardSearch.bind(this)}/>:
                    <ul className="top-nav">
                        <li className={this.props.currentpage===0?"current-page":null}><a href="javascript:void(0)" onClick={this.props.onChangePage.bind(null,0)}>首页</a></li>
                        <li className={this.props.currentpage===1?"current-page":null}><a href="javascript:void(0)" onClick={this.props.onChangePage.bind(null,1)}>关于</a></li>
                        <li className={this.props.currentpage===2?"current-page":null}><a href="javascript:void(0)" onClick={this.props.onChangePage.bind(null,2)}>推荐</a></li>
                        <li className={this.props.currentpage===3?"current-page":null}><a href="javascript:void(0)" onClick={this.props.onChangePage.bind(null,3)}>联系</a></li>
                        <li className={this.props.currentpage===4?"current-page":null}><a href="javascript:void(0)" onClick={this.props.onChangePage.bind(null,4)}>活动</a></li>
                        <li className={this.props.currentpage===5?"current-page":null}><a href="javascript:void(0)" onClick={this.props.onChangePage.bind(null,5)}>商店</a></li>
                    </ul>

                    }
                    <div className="nav-aside">
                        <a href="javascript:void(0)" className={this.state.searchcolumn?"search-bar-active":null} onClick={this.openSearchBar.bind(this)}>
                            <svg className="icon" width="35px" height="35.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                <path fill="#ffffff" d="M621.664 653.664c-44.48 31.68-98.88 50.336-157.664 50.336-150.208 0-272-121.792-272-272s121.792-272 272-272c150.208 0 272 121.792 272 272 0 75.104-30.432 143.104-79.68 192.32l191.904 191.904c8.8 8.8 8.576 22.912-0.256 31.744-8.896 8.896-23.04 8.96-31.744 0.256l-194.592-194.592zM464 672c132.544 0 240-107.456 240-240s-107.456-240-240-240c-132.544 0-240 107.456-240 240s107.456 240 240 240l0 0z" />
                            </svg> 
                        </a>
                        <a href="javscript:void(0)" onClick={this.props.onToTheSubpage.bind(null,'shoppingList')}>
                            <svg className="icon" width="30px" height="35.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                <path fill="#ffffff" d="M849.368 696.616 328.115 696.616c-12.148 0-22.577-8.681-24.766-20.631L200.621 117.362 85.595 117.362c-13.911 0-25.188-11.276-25.188-25.185 0-13.909 11.277-25.186 25.188-25.186l136.006 0c12.15 0 22.576 8.683 24.767 20.634L349.099 646.25l500.274 0c13.908 0 25.185 11.272 25.185 25.183C874.552 685.34 863.275 696.616 849.368 696.616zM816.978 573.155 310.851 573.155c-13.908 0-25.184-11.276-25.184-25.188 0-13.91 11.276-25.187 25.184-25.187l488.531 0L900.364 247.18 354.382 247.18c-13.907 0-25.183-11.276-25.183-25.188 0-13.906 11.276-25.184 25.183-25.184l582.023 0c8.229 0 15.937 4.021 20.648 10.758 4.708 6.738 5.827 15.359 3 23.095L840.623 556.635C836.986 566.556 827.542 573.155 816.978 573.155zM365.132 961.009c-55.177 0-100.075-44.91-100.075-100.099 0-55.166 44.898-100.048 100.075-100.048 55.178 0 100.076 44.882 100.076 100.048C465.205 916.099 420.309 961.009 365.132 961.009zM365.132 811.23c-27.411 0-49.705 22.279-49.705 49.68 0 27.425 22.297 49.728 49.705 49.728 27.409 0 49.706-22.303 49.706-49.728C414.838 833.51 392.54 811.23 365.132 811.23zM800.797 961.009c-55.178 0-100.076-44.91-100.076-100.099 0-55.166 44.898-100.048 100.076-100.048 55.188 0 100.098 44.882 100.098 100.048C900.894 916.099 855.985 961.009 800.797 961.009zM800.797 811.23c-27.411 0-49.709 22.279-49.709 49.68 0 27.425 22.298 49.728 49.709 49.728 27.424 0 49.727-22.303 49.727-49.728C850.523 833.51 828.219 811.23 800.797 811.23z" />
                            </svg>
                        </a>
                    </div>
                </div>
            </nav>
        )
    }
}
export default topNav


